import { Component, OnInit } from '@angular/core';
import { Menu } from './BusinessObjects/BAL';
import { DataService } from './services/data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  constructor(private dataService: DataService) { }
  title = 'Jai Ganesh Cafe';
  brandName: string = "Jai Ganesh Cafe";
  copyrightInfo:string=`Copyright © ${(new Date()).getFullYear()} Jai Ganesh Cafe - All Rights Reserved.`;
  menuItems: Menu[] = [];
  ngOnInit() {
        this.dataService.getMenuItems().subscribe(
      (data) => {
        this.menuItems = data;
        // console.log(JSON.stringify(this.menuItems));
      }
    );
  }


}
